@keyframes animate {

  0%{
      transform: translateY(0) rotate(0deg);
      opacity: 1;
      border-radius: 0;
  }

  100%{
      transform: translateY(-1000px) rotate(720deg);
      opacity: 0;
      border-radius: 50%;
  }

}

@keyframes randomAnimate {
  0% {
    transform: scale(1);
    background: #94DAFF;
  }
  50% {
    transform: scale(1.05);
    background: #FBF8F1;
  }
  100% {
    transform: scale(1);
    background: #94DAFF;
  }
}

@keyframes wrongAnimate {
  0% {
    transform: scale(1);
    background: #94DAFF;
  }
  50% {
    transform: scale(1.05);
    background: #f8adad;
  }
  100% {
    transform: scale(1);
    background: #94DAFF;
  }
}

.fade-in {
  animation: fadeIn 1s ease;
  animation-fill-mode: forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0
  }
  100% {
