$char-w: 1ch;
$gap: .5*$char-w;
$n-char: 10;
$in-w: $n-char*($char-w + $gap);

.game-input {
	display: block;
	margin: 10px auto;
	border: none;
	padding: 0;
	width: $in-w;
	background: repeating-linear-gradient(90deg, 
		white 0, white $char-w, 
		transparent 0, transparent $char-w + $gap) 
		0 100%/ #{$in-w - $gap} 2px no-repeat;
	font: 2.5ch droid sans mono, consolas, monospace;
	letter-spacing: $gap;
	
	&:focus {
		outline: none;
		color: white;
	}
}

.game-button {
  text-align: center;
  display: inline-block;
  padding: 10px 40px;
  border: 1px solid #fff;
  cursor: pointer;
  letter-spacing: 2.5px;
  position: relative;
  overflow: hidden;
  margin: 0 20px;
  background: transparent;
  color: white;
  font-family: 'Orbitron', sans-serif;
}

.game-button:before {
  content: "";
  position: absolute;
  height: 150px;
  width: 50px;
  background : #fff;
  left: -55px;
  top: -40px;
  transform: rotate(37deg);
  transition: all .3s;
  opacity: 0.3;
}

.game-button:hover:before {
  left:95%;
}

.back {
  background: none;
  border: 0;
  position: absolute;
  height: 60px;
  width: 60px;
  top: -10px;
}

.top-left,
.top-right,
.bottom-left,
.bottom-right,
.stalk {
  display: block;
  position: absolute;
  height: 0.2rem;
  width: 1.2rem;
  background: white;
  border-radius: 0.2rem;
  z-index: 2;
  opacity: 1;
  left: 50%;
}

.top-left {
  transform: rotate(45deg);
  top: 2.4rem;
}

.top-right {
  transform: rotate(135deg);
  top: 2.4rem;
}

.bottom-left {
  transform: rotate(135deg);
  top: 3.2rem;
}

.bottom-right {
  transform: rotate(45deg);
  top: 3.2rem;
}

.top-left, .bottom-left {
  margin-left: -1rem;
}

.top-right, .bottom-right {
  margin-left: -0.2rem;
}

.stalk {
  top: 2.8rem;
  width: 2rem;
  opacity: 0;
}



.back--one {
  .top-left, .bottom-left {
    transition: opacity .3s 1s;
  }

  .top-right, .bottom-right {
    transition: margin-left .5s .5s;
  }

  .stalk {
    transition: margin-left .5s .5s, opacity .3s 1s;
  }

  .text {
    transition: opacity .3s 1s;
  }

  &:hover {
    .text {
      opacity: 1;
      transition: opacity .3s .5s;
    }
    .top-left, .bottom-left {
      opacity: 0;
      transition: opacity .3s .5s;
    }
    .top-right, .bottom-right {
      margin-left: -1.3rem;
      transition: margin-left .5s .8s;
    }
    .stalk {
      opacity: 1;
      margin-left: -1.2rem;
      transition: margin-left .5s .8s, opacity .3s .5s;
    }
  }
}
 
